.wrapper {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: var(--font-family);
}

.container {
    width: var(--default-layout-width);
    max-width: 100%;
    min-height: 1000px;
    margin-top: var(--default-layout-header-height);
    display: flex;

}

.content {
    // margin-left: 24px;
    flex: 1;
}

// Loading 
.loading {
    position: relative;
    margin: 24px 0;
    margin-bottom: 124px;
    width: 100%;

    span {
        position: absolute;
        left: 50%;
        content: '';
        background-color: #fe2c55;
        width: 18px;
        height: 18px;
        border-radius: 50%;
        animation: loading 0.8s linear infinite;
        // mix-blend-mode: multiply;

    }

    span:first-child {
        animation-delay: -0.4s;
    }

    span:nth-child(2) {
        background-color: #20d5ec;
    }

    span:last-child {
        animation: loading2 0.8s linear infinite;
        animation-delay: -0.4s;

    }

}

@keyframes loading {

    0% {
        transform: translateX(0);
        transform: translateX(0.9);

    }

    50% {
        transform: translateX(18px);
        transform: translateX(1);

    }

    100% {
        transform: translateX(0);
        transform: translateX(0.9);

    }


}

@keyframes loading2 {

    0% {
        opacity: 1;
        transform: translateX(0);
        transform: translateX(0.9);

    }

    49.99% {
        transform: translateX(18px);
        opacity: 1;
        transform: translateX(1);

    }

    50% {
        transform: translateX(18px);
        transform: translateX(1);

        opacity: 0;


    }

    100% {
        opacity: 0;
        transform: translateX(0);
        transform: translateX(0.9);

    }


}